@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;800&display=swap');

* {
    margin: 0;
    padding: 0;
    user-select: none;
}

$card__color: #480069d5;
$section__color: #56037cd5;
$server__color: rgba(144, 0, 255, 0.92);
$hover_color: rgba(107, 0, 189, 0.92);


/*
display: grid;
grid-template-columns: .4fr 1.5fr repeat(4, 1.2fr);
*/

.container {

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    min-height: 100vh;

    .header-card {

        display: flex;
        justify-content: flex-start;
        align-items: center;

        background-color: $card__color;
        width: 541px;
        height: 65px;
        border-top-right-radius: .7vh;
        border-top-left-radius: .7vh;
        box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;

        .icon {

            margin-right: 2vh;
            color: rgb(255, 255, 255);
            font-size: 1.2em;
            border: 5px $server__color solid;
            border-style: dashed;
            border-radius: 5vh;
            padding: 10px;
            margin-left: 1.5vh;

        }

        .title-section {

            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            flex-direction: column;

            .title {

                font-family: 'Montserrat', sans-serif;
                font-weight: 800;
                color: #fff;
                font-size: 1em;
            }

            .subtitle {

                font-family: 'Montserrat', sans-serif;
                font-weight: 300;
                color: #fff;
                font-size: .7em;
            }
        }
    }

    .center-card {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        background-color: $card__color;
        width: 541px;
        padding-bottom: 10px;

        margin-top: .3vh;
        border-bottom-right-radius: .7vh;
        border-bottom-left-radius: .7vh;
        box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;

        .links {

            display: grid;
            grid-template-columns: .4fr 1.3fr repeat(3, .9fr) 1fr;
            justify-items: center;
            align-items: center;

            width: 94%;
            padding: 10px;
            margin-top: 1vh;
            background-color: $section__color;
            border-radius: .3vh;

            font-family: 'Montserrat', sans-serif;
            font-weight: 500;
            font-size: .8em;

            color: #fff;

        }

        .lists {

            display: grid;
            grid-template-columns: .4fr 1.3fr repeat(3, .9fr) 1fr;
            gap: 4px 0px;
            justify-items: center;
            align-items: center;

            width: 94%;
            padding: 10px;
            margin-top: 1vh;

            font-family: 'Montserrat', sans-serif;
            font-weight: 500;
            font-size: .8em;

            background-color: $section__color;
            border-radius: .3vh;
            color: #fff;
        }

        .buttons {

            display: flex;
            justify-content: flex-start;
            align-items: center;

            div {

                background-color: $server__color;
                padding: 2px 2.6px 2px 2.6px;
                border-radius: .5vh;
                font-size: .9em;
                transition: 0.5s;

                &:hover {
                    background-color: $hover_color;
                }
            }

            .teleport {

                margin-left: 1vh;
            }
        }
    }
}